<template>
    <div class="res-page">
        <el-pagination 
          layout="prev, pager, next" 
          next-text="下一页" 
          prev-text="上一页" 
          :total="total"
          :current-page="currentPage"
          :page-size="pageSize"
          @current-change="handleChange"
        >
        </el-pagination>
    </div>
</template>

<script>
export default {
  props:{
    total:{
      type: Number,
      default:20
    },
    currentPage:{
      type:Number,
      default:1
    },
    pageSize:{
      type:Number,
      default:10
    }
  },
  methods:{
    handleChange(currentPage){
      this.$emit("onSizeChange", currentPage)
    }
  }
};
</script>

<style>
.res-page {
    text-align: center;
    padding: 20px 0;
}
.res-page > span {
    cursor: pointer;
    font-size: 13px;
    position: relative;
    top: -2px;
}
.res-page .el-pagination .btn-next,
.res-page .el-pagination .btn-prev {
    background: none;
}
.res-page .el-pagination button:disabled {
    background: none;
}
.res-page .el-pager li {
    background: none;
    height: 32px;
    width: 32px;
    min-width: 32px;
    line-height: 32px;
    border-radius: 50%;
    padding: 0;
    margin: 0 5px;
}
.res-page .el-pager li.active {
    color: #fff;
    background: #8f9399;
}
.res-page .el-pager li:hover {
    color: #8F9399;
    background: rgba(143, 147, 153, 0.2);
}
</style>